/* Common Button Styles */
.common-button {
  background-color: #007bff;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  text-decoration: none; /* Remove underline */
  font-size: 14px; /* Adjust the font size as needed */
}


/* Button Container */
.button-container {
  margin: 10px;
  display: flex;
  justify-content: left; /* Horizontal alignment */
  align-items: center; /* Vertical alignment */
}

/* Mini Modal Styles */
.mini-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000; /* Set a higher z-index value */
}

.mini-modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  max-width: 80%;
  text-align: left;
}

.mini-modal-content h2 {
  margin-top: 0;
}

.mini-modal-content p {
  margin: 5px 0;
}

.mini-modal-content a {
  color: #007bff;
  text-decoration: none;
}

.mini-modal-content button {
  background-color: #007bff;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

/* Target weekday headers */
.react-calendar__month-view__weekdays {
  display: flex;
  justify-content: space-around; /* To evenly distribute weekdays */
}

/* Style each individual weekday header cell */
.react-calendar__month-view__weekdays__weekday {
  width: calc(100% / 7); /* To make each cell take up 1/7th of the width */
}
/* Target calendar days */
.react-calendar__month-view__days {
  display: flex;
  flex-wrap: wrap; /* Allow days to wrap to the next row if necessary */
}

/* Style each individual day cell */
.react-calendar__tile {
  width: calc(100% / 7); /* To make each cell take up 1/7th of the width */
}

.word-cloud {
  margin-top: 20px;
  text-align: center;
}

.tag {
  display: inline-block;
  margin: 5px;
  padding: 5px 10px;
  background-color: #007bff;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.tag.selected {
  background-color: #0056b3;
}

.tag:hover {
  background-color: #0056b3;
}

.poster {
  background-color: #f0f0f0; /* Set your desired background color */
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: left;
  max-width: 75%; /* Adjust the maximum width as needed */
}

.poster-content {
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 1em; /* You can adjust this value as needed */
}


.poster-modal {
  background-color: #f0f0f0; /* Set your desired background color */
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: left;
  max-width: 300px; /* Adjust the maximum width as needed */
}

.date-box {
  display: flex;
  justify-content: left; /* Horizontal alignment */
  align-items: left; /* Vertical alignment */
}

pre {
